.social-list {
	display: flex;
	align-items: center;

	&__item {
		color: $icon-color;

		&:not(:last-child) {
			margin-right: 8px;

			@include media($screen-md) {
				margin-right: 16px;
			}
		}
	}

	&__link {
		display: block;
		width: 20px;
		height: 20px;

		@include media($screen-md) {
			width: 30px;
			height: 30px;
		}

		@include hover {
			&:hover {
				svg {
					color: $icon-color--hover;
				}
			}
		}

		&:active,
		&:focus {
			svg {
				opacity: 0.8;
				outline: none;
				border: none;
			}
		}

		svg {
			transition: color $anim;
		}

		&--dou {
			width: 31px;
			height: 20px;
		}
	}
}
