// colors
$theme-color: #005395;
$theme-color--light: #27659F;
$theme-color--lighten: #2C7ECB;
$theme-color--important: #2797FF;
$theme-color--important-hover: #30D4F9;

$text-color: #fff;
$text-color--hover: rgba(255, 255, 255, 0.5);

$icon-color: rgba(255, 255, 255, 0.5);
$icon-color--hover: #fff;

$text-color--dark: #93b2cf;
$text-color--shadow: #004485;

$white: #fff;

//media
$screen-ex-sm: 360px;
$screen-sm: 568px;
$screen-md: 768px;
$screen-md-max: 1023px;
$screen-lg: 1024px;
$screen-xl: 1360px;
$screen-xxl: 1680px;

// padding
$padding--xxl: 60;
$padding--xl: 50;
$padding--lg: 40;
$padding--xs: 16;

//other
$anim: 0.3s ease-in;

//retina
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

