.header {
	position: relative;
	overflow: hidden;
	background: $theme-color--light;
	opacity: 0;
	transform: translateY(-100%);
	transition: transform 0.45s ease-out, opacity 0.3s;

	&__wrap {
		min-height: 52px;
		height: 1px;
		display: flex;
		align-items: center;
		justify-content: flex-end;

		@include media($screen-md) {
			min-height: 62px;
		}

		@include media($screen-xl) {
			min-height: 70px;
		}
	}

	&__nav {
		justify-content: flex-end;
		width: calc(100% - 80px);
		height: 100%;
	}

	&__logo {
		margin-right: auto;
		//transform: translate3d(-20%, 0, 0);
		//transition: all .6s;
	}

	&__language {
		margin-left: 8px;

		@include media($screen-md) {
			margin-left: 16px;
		}
	}
}

.is-load {
	.header {
		opacity: 1;
		transform: translateY(0);
	}
}
