.hero {
	position: relative;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	padding-top: 8px;
	padding-bottom: 20px;
	overflow: hidden;

	@include media($screen-md) {
		padding-top: 16px;
	}

	@include media-landscape($screen-md-max) {
		justify-content: center;
	}

	@include media($screen-lg) {
		justify-content: center;
	}

	.canvas-main {
		width: 100%;
		height: auto;
		margin-right: 0;
		margin-left: 0;
		object-fit: contain;

		position: relative;
		bottom: -9%;
		max-width: 85%;
		margin-right: auto;
		margin-left: auto;

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape) {
			max-width: 85%;
			bottom: auto;
		}

		@include media($screen-lg) {
			bottom: auto;
			width: auto;
			max-width: 100%;
			margin-top: -20%;
		}


		&:hover {
			cursor: pointer;
		}
	}

	&__container {
		position: relative;
		height: 100%;
		flex-grow: 1;
		//z-index: 1;

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		@include media($screen-lg) {
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}

	&__content {
		position: relative;
		z-index: 1;
		opacity: 0;
		transform: translate(-50%, 0);
		transition-property: transform, opacity;
		transition-duration: 1s, 0.8s;
		transition-delay: 0.3s, 0.7s;
		transition-timing-function: cubic-bezier(0,.92,0,1.02), ease-in;
		will-change: transform;

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			max-width: 55%;
			margin-right: 0;
			margin-left: 0;
		}

		@include media($screen-lg) {
			max-width: 45%;
			margin-right: 0;
			margin-left: 0;
		}
	}

	&__nav {
		display: flex;
		justify-content: center;

		@include media($screen-lg) {
			flex-direction: column;
			align-items: center;
		}

		@include media($screen-xxl) {
			align-items: flex-start;
			justify-content: flex-start;
		}
	}

	&__title {
		display: block;
		margin-bottom: 16px;
		font-family: 'SFUIDisplay', 'Roboto', 'Arial', sans-serif;
		font-weight: 900;
		font-size: 50px;
		line-height: 60px;
		text-align: center;
		color: $text-color;
		text-transform: uppercase;
		@include text-shadow;

		@include media($screen-ex-sm) {
			font-size: 60px;
			line-height: 72px;
		}

		@include media($screen-md) {
			font-size: 80px;
			line-height: 96px;
			@include text-shadow--md;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			margin-bottom: 16px;
			font-size: 54px;
			line-height: 1;
		}


		@include media($screen-lg) {
			margin-bottom: 0;
			font-size: 100px;
			line-height: 1;
			@include text-shadow--lg;
		}

		@include media($screen-xxl) {
			padding-left: 0;
			font-size: 130px;
			line-height: 1;
			text-align: left;
		}

		//@include media($screen-xxl) {
		//	padding-left: 0;
		//}
	}

	&__title-main {
		animation: buttonAnimation 0.7s cubic-bezier(.17,.67,.72,1.53);
		transform-origin: center bottom;
	}

	&__title-small {
		display: block;
		font-size: 20px;
		line-height: 1;
		text-align: inherit;
		color: $text-color;

		@include media($screen-ex-sm) {
			font-size: 30px;
			line-height: 36px;
		}

		@include media($screen-md) {
			font-size: 50px;
			line-height: 60px;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			font-size: 24px;
			line-height: 1;
		}

		@include media($screen-lg) {
			font-size: 70px;
			line-height: 84px;
		}

		@include media($screen-xl) {
			font-size: 100px;
			line-height: 1;
		}
	}

	&__sub-title {
		display: none;
		margin-bottom: 40px;
		font-family: 'SFUIDisplay', 'Roboto', 'Arial', sans-serif;
		font-weight: 900;
		font-size: 18px;
		line-height: 22px;
		text-align: center;
		color: $text-color;
		@include text-shadow--md;

		@include media($screen-lg) {
			display: block;
		}

		@include media($screen-xl) {
			margin-bottom: 45px;
			padding-left: 76px;
			font-size: 24px;
			line-height: 29px;
			text-align: left;
		}

		@include media($screen-xxl) {
			padding-left: 0;
		}

	}

	&__nav-link {
		width: 100px;
		height: 100px;

		@include media($screen-md) {
			width: 152px;
			height: 152px;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			width: 75px;
			height: 75px;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: 568px) and (orientation: landscape) {
			width: 70px;
			height: 70px;
		}

		@include media($screen-lg) {
			width: auto;
			min-width: 300px;
			height: auto;
			padding-top: 8px;
			padding-bottom: 8px;
		}

		@include media($screen-xxl) {
			min-width: 350px;
		}

		&:not(:last-child) {
			margin-right: 40px;

			@include media($screen-md) {
				margin-right: 124px;
			}

			@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
				margin-right: 24px;
			}

			@include media($screen-lg) {
				margin-right: 0;
				margin-bottom: 16px;
			}
		}
	}


	&__canvas {
		position: absolute;
		top: auto;
		bottom: 0;
		bottom: -20px;
		left: 50%;
		transform: translate(-50%,0%);
		width: 100%;
		height: 100%;
		object-fit: contain;
		z-index: 0;

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			//position: absolute;
			//top: 50%;
			//left: 76%;
			//transform: translate(-50%, -50%);
			//z-index: 0;
			//width: 320px !important;
			//height: 320px !important;
			//object-fit: contain;

			position: absolute;
			top: 19%;
			bottom: auto;
			left: 76%;
			-webkit-transform: translate(-50%,-50%);
			transform: translate(-50%,-50%);
			z-index: 0;
			width: 320px!important;
			height: 320px!important;
			-o-object-fit: contain;
			object-fit: contain;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: 568px) and (orientation: landscape)  {
			top: 17%;
			bottom: auto;
		}

		@include media($screen-lg) {
			display: flex;
			align-items: center;
			position: absolute;
			top: 50%;
			bottom: auto;
			height: auto;

			//left: calc(50% + 200px);
			//transform: translate(-50%, -50%);

			//right: 0;
			//transform: translate(0%,-50%);

			right: auto;
			left: 70%;
			transform: translate(-50%,-50%);

			z-index: 0;
			object-fit: contain;


			top: 50%;
			width: 50%;
			right: auto;
			left: 70%;
			transform: translate(-50%,-50%);
		}

		&:hover {
			cursor: pointer;
		}
	}

	&__video {
		position: absolute;
		top: auto;
		left: 50%;
		bottom: -15px;
		right: 0;
		transform: translate(-50%, 0%);
		display: none;
		width: 95%;
		height: auto;
		margin: auto;
		object-fit: cover;

		@include media($screen-md) {
			width: 600px;
			bottom: 0;
		}

		@include media($screen-lg) {
			top: 0;
			transform: translate(-50%, -9%);
			width: 600px;
			height: 600px;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			//top: -34px;
			//transform: translate(-50%,-9%);
			width: 250px;
			height: 250px;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: 568px) and (orientation: landscape)  {
			//top: -34px;
			//transform: translate(-50%, -9%);
			//width: 300px;
			//height: 300px;
		}

		@include media($screen-xl) {
			transform: translate(-50%, -11%);
			width: 800px;
			height: 800px;
		}

		@include media($screen-xxl) {
			width: 900px;
			height: 900px;
		}
	}

	&__img-canvas {
		display: none;
	}
}







