.language-list {
	display: flex;

	&__item {
		&:not(:last-child) {
			margin-right: 8px;

			@include media($screen-md) {
				margin-right: 16px;
			}
		}
	}

	&__link {
		display: block;
		font-weight: 900;
		font-size: 14px;
		line-height: 16px;
		color: $icon-color;
		text-transform: uppercase;
		transition: color $anim;

		@include media($screen-lg) {
			font-size: 16px;
			line-height: 19px;
		}

		@include hover {
			&:hover {
				color: $icon-color--hover;
				cursor: pointer;
			}
		}

		&:active,
		&:focus {
			opacity: 0.8;
			outline: none;
			border: 0;
		}

		&.is-active {
			color: $icon-color--hover;
		}
	}
}
