.logo {
	display: flex;
	align-items: center;
	width: 154px;
	height: 32px;

	@include media($screen-md) {
		width: auto;
		height: 40px;
	}

	@include hover {
		&:hover {
			cursor: pointer;

			svg {
				.eye {
					fill: $theme-color--important-hover;
				}
			}
		}
	}

	&:active,
	&:focus {
		svg {
			.eye {
				fill: $theme-color--important-hover;
			}
		}
	}

	&__img {
		object-fit: contain;
		border-style: none;
		width: 32px;
		height: 32px;

		@include media($screen-md) {
			width: 40px;
			height: 40px;
		}

		svg {
			.clip {
				animation: slide 8s infinite;
				fill: red;
			}

			@keyframes slide {
				from {
					transform: translateY(-135px);
				}
				50% {
					transform: translateY(-5px);
				}
				to {
					transform: translateY(-135px);
				}
			}
		}

		&--text {
			width: 98px;
			height: 22px;
			margin-left: 8px;

			@include media($screen-md) {
				width: 123px;
				height: 28px;
				margin-left: 16px;
			}
		}
	}
}
