@mixin media($breakpoint) {
	@media screen and (min-width: $breakpoint) {
		@content;
	}
}

@mixin media-landscape($breakpoint) {
	@media screen and (max-width: $breakpoint) and (orientation: landscape) {
		@content;
	}
}

@mixin text-shadow {
	text-shadow: 2px 2px 0 $text-color--shadow;
}

@mixin text-shadow--md {
	text-shadow: 3px 3px 0 $text-color--shadow;
}

@mixin text-shadow--lg {
	text-shadow: 4px 4px 0 $text-color--shadow;
}


@mixin hover {
	@media(hover: hover) and (pointer: fine) {
		@content;
	}
}

@mixin container {
	width: 100%;
	margin: 0 auto;
	padding: 0 #{$padding--xs}px;

	@include media($screen-lg) {
		max-width: #{944px + ($padding--lg * 2)};
		margin: 0 auto;
		padding: 0 #{$padding--lg}px;
	}

	@include media($screen-xl) {
		max-width: #{1200px + ($padding--xl * 2)};
		margin: 0 auto;
		padding: 0 #{$padding--xl}px;
	}

	@include media($screen-xxl) {
		max-width: #{1380px + ($padding--xxl * 2)};
		margin: 0 auto;
		padding: 0 #{$padding--xxl}px;
	}
}
