.footer {
	position: relative;
	display: flex;
	align-items: flex-end;
	min-height: 62px;
	padding-bottom: 15px;
	font-size: 14px;
	line-height: 1.7;
	background-image: url('../img/bg-footer.png');
	background-repeat: no-repeat;
	background-size: cover;
	opacity: 0;
	transform: translateY(100%);
	transition: transform 0.45s ease-out, opacity 0.3s;

	@include media($screen-md) {
		min-height: 97px;
	}

	@include media($screen-lg) {
		padding-bottom: 20px;
	}

	&__wrap {
		z-index: -1;
		display: flex;
		align-items: center;
		justify-content: space-between;

		@include media($screen-md) {
			padding-right: 8px;
			padding-left: 8px;
		}

		@include media($screen-lg) {
			padding-right: 0;
			padding-left: 0;
		}

		@include media($screen-xl) {
			flex-direction: column;
			align-items: flex-start;
			margin-left: 68px;
		}

		@include media($screen-xxl) {
			margin-left: 110px;
		}
	}

	&__info {
		display: flex;
		flex-wrap: wrap;

		@include media($screen-xl) {
			flex-wrap: nowrap;
		}
	}

	&__menu {
		width: 100%;

		@include media($screen-md) {
			width: 50%;
		}

		@include media($screen-xl) {
			width: 33.33%;
		}
	}

	&__mail {
		font-size: 10px;
		line-height: 12px;

		@include media($screen-lg) {
			font-size: 14px;
			line-height: 16px;
		}
	}

	&__mail-link {
		font-size: inherit;
		line-height: inherit;
		transition: color $anim;

		@include hover {
			&:hover {
				cursor: pointer;
			}
		}

		&:active,
		&:focus {
			opacity: 0.8;
			outline: none;
			border: none;
		}
	}

	&__copyright {
		margin-top: auto;
		font-size: 10px;
		line-height: 12px;

		@include media($screen-lg) {
			font-size: 14px;
			line-height: 16px;
		}

		@include media($screen-xl) {
			padding-bottom: 7px;
		}
	}
}



.is-load {
	.footer {
		opacity: 1;
		transform: translateY(0);
	}
}
