.fun {
	position: relative;
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	padding-top: 16px;
	overflow: hidden;

	@include media($screen-md) {
		padding-top: 24px;
	}

	.slick-carousel {
		&.slick-slide.slick-active {
			display: block;
			height: 100%;
		}

		&.slick-slider {
			display: flex;
			flex-direction: column;
			flex-grow: 1;
			height: 100%;
		}

		.slick-list {
			overflow: initial;
		}

		.slick-list.draggable {
			display: flex;
			flex-direction: column;
			flex-grow: 1;
			height: 100%;
		}

		.slick-track {
			flex-grow: 1;
		}

		.slick-next {
			right: 10px;
			z-index: 1;
		}

		.slick-prev {
			left: 10px;
			z-index: 1;
		}

		.slick-dots {
			bottom: 0;
			z-index: 888;
		}

		.slick-dots li {
			width: 12px;
			height: 12px;
			margin: 0 3px;
		}

		.slick-dots li button:before {
			width: 12px;
			height: 12px;
			font-size: 10px;
			color: #fff;
			opacity: .3;

			@include media($screen-xl) {
				font-size: 12px;
			}
		}

		.slick-dots li.slick-active button:before {
			opacity: 1;
		}

	}

	.slick-dotted.slick-slider {
		margin-bottom: 10px;
		padding-bottom: 20px;
	}

	.canvas-fun {
		width: 100%;
      	margin-right: 0;
      	margin-left: 0;
		height: auto;
		object-fit: contain;

		position: relative;
		margin-top: -10%;

		@include media($screen-md) {
			//position: absolute;
			//top: auto;
			//bottom: 0;
			//left: 50%;
			//transform: translate(-50%,0%);
			//width: 768px;
			//height: 768px;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			margin-top: 0;
			//position: absolute;
			//top: 32%;
			//left: 76%;
			//transform: translate(-50%, -50%);
			//z-index: 0;
			//width: 320px;
			//height: 320px;
			//object-fit: contain;
		}

		@include media($screen-lg) {
			width: auto;
			max-width: 100%;
			margin-top: 0;
		}

		@include media($screen-xxl) {
			//width: 1000px;
			//height: 1000px;
		}

		&:hover {
			cursor: pointer;
		}
	}

	&__container {
		position: relative;
		display: flex;
		flex-direction: column;
        justify-content: center;
		flex-grow: 1;
		height: 100%;

		@include media($screen-lg) {
			//justify-content: center;
		}
	}

	&__title {
        display: block;
        margin-bottom: 16px;
        text-align: center;

        @include media($screen-md) {
            margin-bottom: 24px;
        }

        @include media($screen-lg){
            @include container;
            //position: absolute;
            //top: 0;
            //left: 0;
            text-align: left;
        }

        @include media($screen-xl) {
            margin-bottom: 30px;
        }
	}

	&__descr {
		@include container;

		@include media($screen-md) {
			max-width: 500px;
		}

		@include media($screen-lg) {
			order: -1;
			max-width: 49%;
			//position: absolute;
			//left: 0;
			//top: 50%;
			//transform: translateY(-50%);

			margin: 0;
			padding-right: 0;
			padding-left: 0;
		}

		&.container {
			@include media($screen-lg) {
				margin-right: 0;
				margin-left: 0;
			}
		}
	}

	&__descr-title {
		display: block;
		margin-bottom: 10px;
		font-weight: 900;
		font-size: 20px;
		line-height: 24px;
		text-align: center;
		text-transform: uppercase;
		color: $text-color;
		@include text-shadow;

		@include media($screen-ex-sm) {
			font-size: 30px;
			line-height: 36px;
		}

		@include media($screen-md) {
			font-size: 36px;
			line-height: 43px;
			@include text-shadow--md;
		}

		@include media($screen-lg) {
			font-size: 40px;
			line-height: 48px;
			text-align: left;
			@include text-shadow--lg;
		}

		@include media($screen-xl) {
			margin-bottom: 17px;
			font-size: 60px;
			line-height: 70px
		}
	}

	&__descr-text {
		display: block;
		font-size: 14px;
		line-height: 90%;
		text-align: center;
		opacity: 0;
		transform: translateY(17px);
		transition: transform 0.45s ease-out, opacity 0.3s;

		@include media($screen-lg) {
			text-align: left;
		}

		@include media($screen-xl) {
			font-size: 18px;
		}

		&:not(:last-child) {
			margin-bottom: 12px;
		}

		&:nth-child(1) {
			transition-delay: 0.3s;
		}

		&:nth-child(2) {
			transition-delay: 0.9s;
		}

		&:nth-child(3) {
			transition-delay: 1.2s;
		}
	}

	@keyframes animateText {
		from {
			transform: translateY(17px);
			opacity: 0;
		}
		to {
			transform: translateY(0);
			opacity: 1;
		}
	}


	&__canvas {
		position: relative;
		top: auto;
		bottom: 0;
		left: 50%;
		transform: translate(-50%,0%);

		width: 100%;
		height: auto;
		object-fit: contain;
		transition: opacity 0.3s;

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
			width: 420px !important;
			height: 420px !important;
		}

		@include media($screen-lg) {
			display: flex;
			align-items: center;
			position: absolute;
			top: calc(50% - 100px);

			left: 75%;
			transform: translate(-50%,-50%);

			z-index: 0;
			object-fit: contain;
		}

        @include media($screen-xl) {
			top: calc(50% - 130px);
        }

		&:hover {
			cursor: pointer;
		}

	}

	&__slider {
		text-align: center;
	}

	&__slider-wrap {
		position: relative;
		margin-top: -25%;

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape) {
			margin-top: -19%;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: 568px) and (orientation: landscape) {
			margin-top: -25%;
		}

		@include media($screen-lg) {
			margin-top: 0;
		}
	}

	&__slide-item {
		height: 100%;
		position: relative;

		@include media($screen-lg) {
			@include container;
			min-height: calc(100vh - 310px);
			display: flex !important;
			flex-direction: column;
			flex-grow: 1;
			height: 100%;
			justify-content: center;
		}
	}

}

.is-load {
	.slick-active {
		.fun__descr-text {
			opacity: 1;
			transform: translateY(0);
		}
	}
}

