.accordion {
  &__title {
    display: block;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
  }

  &__descr {
    padding: 0 18px;
    //height: 0;
    //overflow: hidden;
    // transition: height 0.3s ease-out;

    // max-height: 0;
    // overflow: hidden;
    // transition: max-height 0.55s ease-out;

    ul {
      padding-top: 10px;
      list-style-type: disc;
    }

    li {
      font-size: 14px;
      line-height: 1.2;
    }

    a {
      position: relative;
      display: inline-block;
      margin-top: 10px;
      padding-bottom: 2px;
      font-size: 14px;
      line-height: 16px;
      font-weight: bold;
      text-decoration: none;
      color: #30D4F9;

      @include hover {
        &:hover {
          color: $text-color--hover;
        }
      }

      &:active,
      &:focus {
        opacity: 0.8;
        outline: none;
      }

      &::before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: currentColor;
      }
    }
  }

  &__item {
    position: relative;
    display: block;
    padding-top: 13px;
    padding-right: 12px;
    padding-bottom: 13px;
    padding-left: 16px;
    width: 100%;
    text-align: left;
    outline: none;
    font-size: 15px;
    background: rgba(255, 255, 255, 0.1);
    border: 1px solid rgba(255, 255, 255, 0.5);
    box-sizing: border-box;
    border-radius: 5px 0px;

    @include hover {
      &:hover {
        cursor: pointer;

        .accordion__btn {
          color: $text-color--hover;
        }
      }
    }

    &:active {
      opacity: 0.8;
    }

    &:not(:last-child) {
      margin-bottom: 16px;
    }

    &.active {
      .accordion__btn {
        &::after {
          content: '\2212';
        }
      }

      // .accordion__descr {
      //   max-height: none;
      // }
    }

    &.js-accordion-panel {
      display: none;
      // max-height: 0;
      // overflow: hidden;
      // transition: max-height 0.5s ease-out;
    }
  }

  &__btn {
    position: absolute;
    display: block;
    top: 21px;
    right: 10px;
    transform: translateY(-50%);
    width: 18px;
    height: 18px;
    color: #30D4F9;
    font-weight: normal;
    font-size: 30px;
    transition: color 0.3s ease-out;

    &::after {
      content: '\002B';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    @include hover {
      &:hover {
        color: $text-color--hover;
      }
    }

    &:focus {
      color: $text-color--hover;
      outline: none;
      border: 0;
    }

    &:active {
      opacity: 0.8;
    }

  }
}






