.job {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  padding-top: 24px;
  padding-bottom: 24px;
  overflow: hidden;

  &__title {
    display: block;
    margin-bottom: 16px;
    text-align: center;

    @include media($screen-md) {
      margin-bottom: 24px;
    }

    @include media($screen-xl) {
      margin-bottom: 30px;
    }
  }

  &__content {
    @include media($screen-lg) {
      position: relative;
      z-index: 1;
      width: 50%;
    }
  }

  &__canvas {
    position: absolute;
    top: auto;
    bottom: 0;
    left: 50%;
    transform: translate(-50%,0%);
    z-index: 0;
    width: 100%;
    height: auto;
    object-fit: contain;
    display: none;

    //@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape)  {
    //  position: absolute;
    //  top: 19%;
    //  left: 76%;
    //  transform: translate(-50%, -50%);
    //  z-index: 0;
    //  width: 320px !important;
    //  height: 320px !important;
    //  object-fit: contain;
    //}

    //@media screen and (min-device-width: 320px) and (max-device-width: 568px) and (orientation: landscape)  {
    //  top: 17%;
    //}

    @include media($screen-lg) {
      display: flex;
      align-items: center;
      position: absolute;
      top: 50%;

      //left: calc(50% + 200px);
      //transform: translate(-50%, -50%);

      //right: 0;
      //transform: translate(0%,-50%);

      right: auto;
      left: 75%;
      transform: translate(-50%,-50%);

      z-index: 0;
      object-fit: contain;
    }

    &:hover {
      cursor: pointer;
    }
  }

  .canvas-job {
    width: 100%;
    height: auto;
    margin-right: 0;
    margin-left: 0;

    object-fit: contain;

    @media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape) {
      max-width: 100%;
    }

    @include media($screen-lg) {
      width: auto;
      max-width: 100%;
      //margin-top: -20%;
      max-width: 90%;
      margin: auto;
    }


    &:hover {
      cursor: pointer;
    }
  }
}



