html {
	height: 100%;
}

body {
	position: relative;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	margin: 0;
	min-width: 320px;
	font-family: 'Roboto', 'Arial', sans-serif;
	font-size: 16px;
	line-height: 1.25;
	font-weight: 400;
	color: $text-color;
	background-color: $theme-color;
	text-rendering: optimizeLegibility;
	text-decoration-skip: objects;
	-webkit-text-size-adjust: 100%;
	-webkit-font-smoothing: antialiased;
	-webkit-tap-highlight-color: transparent;

	&.is-load:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-image: url('../img/pattern/pattern-bg.svg');
		background-size: 320px auto;
		background-position: 0, 0;
		animation: animatedBackground 1.5s ease-out;
		opacity: 1;
		animation-fill-mode: forwards;

		@include media($screen-lg) {
			background-size: auto;
		}
	}

}

.main {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
}

.container {
	@include container;
}

img {
	max-width: 100%;
	height: auto;
}

.visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	border: 0;
	padding: 0;
	white-space: nowrap;
	clip-path: inset(100%);
	clip: rect(0 0 0 0);
	overflow: hidden;
}


canvas {
	display: block;
	//width: 100%;
	//height: 100%;
	margin: 0;
	object-fit: cover;
}
