.btn-round {
	position: relative;
	padding: 7px;
	transition: all ease-in-out 300ms;

	animation-name: buttonAnimation;
	animation-duration: 1s;
    animation-timing-function: cubic-bezier(0.17, 0.67, 0.72, 1.53);
    animation-delay: 4.1s;
	
	
	transform-origin: center bottom;

	@include media($screen-md) {
		padding: 11px;
	}

	@include media($screen-lg) {
		display: flex;
		justify-content: center;
		height: 60px;
		padding: 0;
	}

	&:hover {
		z-index: 9;

		.btn-round__wrap {
			background-color: $theme-color--important;

			&:before {
				opacity: 0;
			}

			&:after {
				animation-name: 'play_up';
				animation-duration: 1s;
				animation-iteration-count: infinite;
				box-shadow: 0 0 0 3px rgba(0, 0, 0,.5);
			}
		}

		@include media($screen-lg) {
			.btn-round__text {
				font-size: 40px;
			}

			.btn-round__letter {
				display: inline-block;
				//animation: shrinkjumpLetter 1s ease-out;
				transform-origin: bottom center;
			}

			.btn-round__wrap {
				background-image: linear-gradient(178deg, rgba(255, 255, 255, 0.3) calc(100% - 99px) 10%, transparent 12%, transparent 0%);
			}
		}

		transform-origin: center bottom;
		animation-delay: 1.5s;

		.btn-round__wrap {
			@include media($screen-lg) {
				width: 326px;
				height: 66px;
				font-weight: 900;
				font-size: 40px;
				line-height: 48px;
			}

			@include media($screen-xl) {
				width: 388px;
				height: 76px;
			}
		}
	}

	&:first-child {
		transform-origin: center bottom;
		animation-name: buttonAnimation;
		animation-duration: 1s;
		animation-timing-function: cubic-bezier(0.17, 0.67, 0.72, 1.53);
		animation-delay: 3.3s;
	}

	&__wrap {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;
		font-family: 'SFUIDisplay', 'Roboto', 'Arial', sans-serif;
		font-weight: 900;
		font-size: 24px;
		line-height: 29px;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px #004485;
		background-color: $theme-color--lighten;
		box-shadow: 2px 2px 0px #004485;
		border-radius: 50%;
		transition: all 0.2s ease-out;

		@include media($screen-md) {
			font-size: 36px;
			line-height: 43px;
			@include text-shadow;
		}

		@media screen and (min-device-width: 320px) and (max-device-width: $screen-md-max) and (orientation: landscape) {
			font-size: 22px;
			line-height: 1;
		}

		@include media($screen-lg) {
			width: 100%;
			height: inherit;
			font-size: 36px;
			line-height: 43px;
			border-radius: 0;
			background-image: linear-gradient(178deg, rgba(255, 255, 255, 0.2) calc(100% - 99px) 10%, transparent 12%, transparent 0%);
			box-shadow: 4px 4px 0px #004485;
		}

		@include media($screen-xxl) {
			width: 100%;
			height: inherit;
		}

		&::after {
			content: '';
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: calc(100% + 14px);
			height: calc(100% + 14px);
			border: 1px solid $theme-color--lighten;
			border-radius: inherit;
			transition: all ease-in-out 300ms;

			@include media($screen-lg) {
				display: none;
			}
		}

		&::before {
			content: '';
			position: absolute;
			top: calc(50% + 2px);
			left: calc(50% + 2px);
			transform: translate(-50%, -50%);
			width: calc(100% + 14px);
			height: calc(100% + 14px);
			border: 1px solid #004485;
			border-radius: inherit;

			@include media($screen-lg) {
				display: none;
			}
		}

	}

	&__text {
		z-index: 1;
	}
}


@keyframes shrinkjumpLetter {
	10%, 35% {
		transform: scale(2, .2) translate(0, 0);
	}

	45%, 50% {
		transform: scale(1) translate(0, -20px);
	}

	80% {
		transform: scale(1) translate(0, 0);
	}
}


@keyframes buttonAnimation {
	0%, 30%{
		transform: scale(1.4, .3)
	}
	30%, 40%{
		transform: scale(1.2, 1)
	}
	30%, 50%{
		transform: scale(1, 1.2)
	}
	50%, 70%{
		transform: scale(1.1, 1)
	}
	//70%, 90%{
	//	transform: scale(1, 1.1)
	//}
	70%, 100%{
		transform: scale(1, 1)
	}
}


@keyframes  play_up {

	0%
	{
		border-color: rgba(39, 151, 255, 0.85);
		border-width: 3px;
		box-shadow: 0 0 rgba(39, 151, 255, 0.46);
	}
	to
	{
		border-color: rgba(39, 151, 255, 0);
		border-width: 0px;
		box-shadow: 0 0 0 8px rgba(39, 151, 255,0);
	}

}
